import './App.css';
import React, { Component } from 'react'
import Navbar from './MyComponents/Navbar';
import NewsItems from './MyComponents/NewsItems';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'


export class App extends Component {
  render() {
    return (
      <div>
        <Router>
          <Navbar />
          <Switch>
            <Route exact path="/"><NewsItems key="General" pageSize={5} category="General" /></Route>
            <Route exact path="/business"><NewsItems key="business" pageSize={5} category="business" /></Route>
            <Route exact path="/entertainment"><NewsItems key="entertainment" pageSize={5} category="entertainment" /></Route>
            <Route exact path="/general"><NewsItems key="general" pageSize={5} category="general" /></Route>
            <Route exact path="/health"><NewsItems key="health" pageSize={5} category="health" /></Route>
            <Route exact path="/science"><NewsItems key="science" pageSize={5} category="science" /></Route>
            <Route exact path="/sports"><NewsItems key="sports" pageSize={5} category="sports" /></Route>
            <Route exact path="/technology"><NewsItems key="technology" pageSize={5} category="technology" /></Route>
          </Switch>
        </Router>
      </div>
    )
  }
}

export default App
