import React, { Component } from 'react'
import News from './News'
import Spinner from './Spinner';
import PropTypes from 'prop-types'
import InfiniteScroll from 'react-infinite-scroll-component';



export class NewsItems extends Component {

    fetchMoreData = async () => {
        this.setState({ page: this.state.page + 1 })
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&language=en&apiKey=45488bf1705c43ee99f1fea7be36f3b4&page=${this.state.page}&pageSize=${this.props.pageSize}`
        let data = await fetch(url);
        let parsedata = await data.json();
        this.setState({
            articles: this.state.articles.concat(parsedata.articles),
            totalResults: parsedata.totalResults,
            loading: false
        })
    }

    static defaultProps = {
        pageSize: 10,
        category: 'general',
    }

    static propTypes = {
        pageSize: PropTypes.number,
        category: PropTypes.string,
    }

    constructor() {
        super()
        this.state = {
            articles: [1, 2, 3],
            page: 1,
            loading: false
        }
    }
    async componentDidMount() {
        this.setState({ loading: true });
        document.getElementById("toploader").style.width = '0vw';
        document.getElementById("toploader").style.display = 'block';
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&language=en&apiKey=45488bf1705c43ee99f1fea7be36f3b4&page=${this.state.page}&pageSize=${this.props.pageSize}`
        document.getElementById("toploader").style.width = '10vw';
        let data = await fetch(url);
        // document.getElementById("toploader").style.width = '50vw';
        document.getElementById("toploader").style.width = '100vw';
        let parsedata = await data.json();
        this.setState({ articles: parsedata.articles, totalResults: parsedata.totalResults, loading: false })
        setTimeout(() => {
            document.getElementById("toploader").style.display = 'none';
        }, 500);
    }

    // prevBtnClick = async () => {
    //     await this.setState({ page: this.state.page - 1 })
    //     await this.componentDidMount();
    // }
    // nextBtnClick = async () => {
    //     await this.setState({ page: this.state.page + 1 })
    //     await this.componentDidMount();
    // }



    render() {
        return (
            <div className="news_items_page">
                <h1>NewsToday - Top Headliness</h1>
                {/* {this.state.loading && <Spinner />} */}
                <InfiniteScroll
                    dataLength={this.state.articles.length}
                    next={this.fetchMoreData}
                    hasMore={this.state.articles.length !== this.state.totalResults}
                    loader={<Spinner />}
                >
                    <div className="news_items">
                        {
                            !this.state.loading && this.state.articles.map((element) => {
                                console.log(element);
                                return <News title={element.title} desc={element.description} img={element.urlToImage} url={element.url} other={!element.author ? "Unknown " + element.publishedAt : element.author + " " + element.publishedAt} source={element.source && element.source.name} />
                            })
                        }
                    </div>
                </InfiniteScroll>
                {/* <div className="next_prev_btns">
                    <button disabled={this.state.page <= 1} className="prev_btn" onClick={this.prevBtnClick}> ← Prev</button>
                    <button disabled={this.state.page >= Math.ceil(this.state.totalResults / this.props.pageSize)} className="next_btn" onClick={this.nextBtnClick}>Next → </button>
                </div> */}
            </div>
        )
    }
}

export default NewsItems
