import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Toploader from './Toploader';



export class Navbar extends Component {

    constructor() {
        super();
        this.state = {
            ulStyle: undefined,
            middleStyle: undefined,
            leftStyle: undefined
        }
    }

    menuButtonClick = () => {
        if (this.state.ulStyle === undefined) {
            this.setState({ ulStyle: { width: 'auto', paddingLeft: '0%', paddingTop: '0px', opacity: '1', visibility: 'visible', height: '160px', marginTop: '10px', transition: '1s', overflowX: 'scroll' }, middleStyle: { position: 'fixed', justifyContent: 'unset', alignItem: 'unset' }, leftStyle: { display: 'none !important' } });
        } else {
            this.setState({ ulStyle: undefined })
        }
    }

    render() {
        return (
            <div>
                <header>
                    <div className="left" id="left" style={this.state.leftStyle}>NewsToday</div>
                    <div className="middle" id="middle">
                        <ul style={this.state.ulStyle}>
                            <li id="homemenu" ><Link className="menus" to="/">Home </Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/about">About </Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/entertainment">Entertainment</Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/general">General</Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/health">Health</Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/sports">Sports</Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/science">Science</Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/technology">Technology</Link></li>
                            <li id="aboutmenu"><Link className="menus" to="/business">Business</Link></li>
                        </ul>
                    </div>
                    <div className="right">
                        <div className="menubtn" onClick={this.menuButtonClick}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" id="menubtn" fill="currentColor"
                                className="bi bi-list" viewBox="0 0 16 16">
                                <path fillRule="evenodd"
                                    d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z" />
                            </svg>
                        </div>
                    </div>
                </header>
                    <Toploader/>
            </div>
        )
    }
}

export default Navbar
