import React, { Component } from 'react'

export class News extends Component {
    static defaultProps = {
        source : 'Unknown'
    }

    render() {
        let {title,desc,img,url,other} = this.props;
        return (
            <div className="note">
                <div className="note_image">
                <img src={!img?"/def_image.jpg":img} alt=""/>
                <div className="note_source">{this.props.source}</div>
                </div>
                <div className="note_title">{title}</div>
                <div className="note_desc">{desc}</div>
                <div className="note_other">By {other}</div>
                <div className="drbtn">
                    <button id="readbtn"><a href={url} target="_">Read</a></button>
                </div>
            </div>
        )
    }
}

export default News
